import { Typography } from "@material-ui/core";
import React, { Component } from "react";
import { Link, Router, Switch, Route, BrowserRouter } from "react-router-dom";
import MenuRoundedIcon from "@material-ui/icons/MenuRounded";
import "./Navbar.scss";
import { Main } from "../Main/Main";
import { About } from "../About/About";
export default class Navbar extends Component {
  slide = () => {
    const buttonSlide = document.querySelector(".links");
    buttonSlide.classList.toggle("slide");
  };
  render() {
    return (
      <div className="root">
        <div className="nav">
          <div className="logo">
            <Typography variant="h5">React Website</Typography>
          </div>
          <div className="links">
            <a href="#">Home</a>
            <a href="#about">About</a>
          </div>
          <div className="button" onClick={this.slide}>
            <MenuRoundedIcon
              style={{
                width: "1.3em",
              }}
            />
          </div>
        </div>
      </div>
    );
  }
}
