import { Grid, Paper, Typography } from "@material-ui/core";
import React from "react";
import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
import "./About.scss";
import Reactimage from "./react-img.png";
export const About = () => {
  return (
    <div className="about" id="about">
      <Typography
        variant="h1"
        style={{
          textTransform: "uppercase",
          textAlign: "center",
          paddingTop: "20px",
        }}
      >
        About
      </Typography>
      <Grid container justify="center" alignItems="center" className="grid-about">
        <Grid item xl={6} lg={6} md={6} sm={12} xs={12}>
          <Paper
            className="paper-about"
            elevation={0}
            square
            style={{
              textAlign: "center",
            }}
          >
            <Typography variant="h2">Why Learn React?</Typography>
            <Typography
              variant="body2"
              className="text-about"
              style={{
                margin: "20px",
              }}
            >
              Lorem ipsum, dolor sit amet consectetur adipisicing elit. Qui doloribus aut vitae sit
              numquam consequatur quos sapiente porro debitis cupiditate! Lorem ipsum dolor sit amet
              consectetur, adipisicing elit. Dignissimos laboriosam iure recusandae praesentium
              consequatur nisi totam? Tempora recusandae vero unde culpa nihil eaque omnis nulla ad
              rerum, eligendi facilis quaerat.
            </Typography>
          </Paper>
        </Grid>
        <Grid item xl={6} lg={6} md={6} sm={12} xs={12}>
          <Paper
            className="paper-about"
            elevation={0}
            square
            style={{
              textAlign: "center",
            }}
          >
            <img src={Reactimage} className="img-about" />
          </Paper>
        </Grid>
      </Grid>
    </div>
  );
};
